.Panel::after {
	background-image: url(./img/backgroundLight.png);
	background-size: cover;
}
body[scheme="space_gray"] .Panel::after {
	background-image: url(./img/backgroundDark.png);
	background-size: cover;
}
body[scheme="space_gray"] .Group>.Cell:first-child, .Group>.CellButton:first-child, .Group>.InfoRow:first-child {
	color: #fff;
}
.Panel .Panel__in {
	background-color: transparent;
}

@media screen and (max-width: 333px) {
	.Button--sz-l .Button__content {
		font-size: 13px;
	}
}
@media screen and (max-width: 313px) {
	.Button--sz-l .Button__content {
		font-size: 11px;
	}
}
@media screen and (max-width: 298px) {
	.Button--sz-l .Button__content {
		font-size: 9px;
	}
}
@media screen and (max-width: 283px) {
	.Button--sz-l .Button__content {
		font-size: 8.2px;
	}
}